.containerModal {
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background-color: rgba(0, 0, 0, 0.5);
  display: none;
}

.contentModal {
  padding: 25px;
  background: white;
  min-width: 250px;
  border-radius: 16px ;
}

.containerModal.active {
  display: flex;
  justify-content: center;
  align-items: center;
}

